import React  from 'react'
import Sound from 'react-sound'
import click from './Images/click.mp3'
import win from './Images/win.mp3'
const PlaySound=(
	props,
	handleSongLoading,
	handleSongPlaying,
	handleSongFinsihedPlaying
	)=>{
	// const [isPlaying,setIsPlaying]=useState(false)
	// console.log(props.music)
	if(props.music==='click'){
	return(
		<>
		{/*<button onClick={()=>setIsPlaying(!isPlaying)}>fsdf</button>*/}
		<Sound
			url={click}
			playStatus={Sound.status.PLAYING}
			playFromPosition={0}
			onloading={handleSongLoading}
			onPlaying={handleSongPlaying}
			onFinishedPlaying={handleSongFinsihedPlaying}
		/>
		</>		
		)}
	else{
		return(
		<>
		{/*<button onClick={()=>setIsPlaying(!isPlaying)}>fsdf</button>*/}
		<Sound
			url={win}
			playStatus={Sound.status.PLAYING}
			playFromPosition={0}
			onloading={handleSongLoading}
			onPlaying={handleSongPlaying}
			onFinishedPlaying={handleSongFinsihedPlaying}
		/>
		</>		
		)
	}
}

export default PlaySound;