import React,{useState} from 'react'
import Data from './Data'
import Todolist from './Todolist'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import './index.css'
import Keep from './Keep'
import Errore from './Error'
import {Route,Switch} from 'react-router-dom'
import Menu from './Menu'
import Contact from './Contact'
import Game from './Game'
import XOX from './XOX'
import Car from './Car'
import Quote from './Quote'
import Typing from './Typing'



const App= () => {
	const [bg,setbg]=useState("white")
	const [tc,settc]=useState("black")
	const clicked=()=>{
		bg==='white' ? setbg("black") : setbg("white")
		tc==='black' ? settc("white") : settc("black")
	}
	return (<>
		<Menu
			fnc={clicked}/>
		<div style={{backgroundColor: bg,color:tc}}>
		<Switch >
			<Route exact path='/' component={Data}/>
			<Route exact path='/website' component={Data}/>
			<Route exact path='/todolist' component={Todolist}/>
			<Route exact path='/keep' component={Keep}/>
			<Route exact path='/contact' component={Contact}/>
			<Route exact path='/game' component={Game}/>
			<Route exact path='/game/API' component={Quote}/>
			<Route exact path='/game/XOX' component={XOX}/>
			<Route exact path='/game/Car' component={Car}/>
			<Route exact path='/game/typing' component={Typing}/>
			<Route component={Errore}/>
		</Switch>
		</div>
		</>);
}

export default App