import React from 'react'
import {NavLink} from 'react-router-dom'


function Data(){
	return (<div className="main_div">
		<div className='center_div' style={{color:'black'}}>
		<br/>
		<h1 style={{fontSize: "50px"}}>Project List</h1>
		<ol>
		<li>Google Keep
		<span style={{marginLeft:'40px',marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/keep' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}> Google Keep features</p>
		<li>To-Do-List
		<span style={{marginLeft:'62px' ,marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/todolist' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}>Daily Task list</p>
		</ol>
		<ol>
		<li>APIs
		<span style={{marginLeft:'117px',marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/game/api' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}> Various APIs</p>
		<li>XOX
		<span style={{marginLeft:'116px' ,marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/game/XOX' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}>Classic Tic Tac Toe Game</p>
		</ol>
		<ol>
		<li>Car game
		<span style={{marginLeft:'68px',marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/game/car' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}> Ride the Lamborgini</p>
		<li>Typing
		<span style={{marginLeft:'95px' ,marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/game/typing' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}>Test your typing?</p>
		</ol>
		<ol>
		<li>Contact
		<span style={{marginLeft:'85px',marginTop:'15px'}} >
		<NavLink exact activeClassName='active_class' to='/contact' className="button_five" style={{marginLeft: '10px'}}>Go</NavLink>
		</span></li>
		<p style={{textAlign: 'left',fontSize: "10px", marginTop: '-5px'}}>Get in touch?</p>
		</ol>
		</div>
		</div>
	);
}

export default Data
