import React,{useState} from 'react'
import axios from 'axios'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
const far=(val)=>{
	return Math.round((val - 273) * 100) / 100;
}
const Joke= ()=>{
	const [cur,setcur]=useState()
	const [city,setCity]=useState()
	const [temp,settemp]=useState()
	const [mtemp,msettemp]=useState()
	const [mmtemp,mmsettemp]=useState()
	const getJoke =async (char = 'Mumbai')=>{
			try{
			let res = await axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${char}&appid=92c2cd8e75c1490df8fa6d986051c9f0`)
			settemp(far(res.data.main.temp))
			msettemp(far(res.data.main.temp_min))
			mmsettemp(far(res.data.main.temp_max))
			console.log(res.data.main)
			}
			catch(e){
				settemp(-273)
			}
	}
	if(!temp){
		getJoke()
		console.log(temp)
	}
	const changes=(event)=>{
		setcur(event.target.value)
	}

	const replay=()=>{
		setCity(cur)
		getJoke(city)
		// setans(data[Math.floor(Math.random()*data.length)])	
	}
	return (<>
		<div style={{display:"flex", flexDirection:"row",alignItems:"center",justifyContent:'center'}} >
		<input type="text" onChange={changes} placeholder="Mumbai" value={cur}/>
		<button onClick={replay} className='button_three' style={{margin:"5px"}}> <NavigateNextIcon/></button>
		</div>
		{
		(temp!==-273) ?
		<div style={{textAlign:'center',paddingTop:"10px"}}>
		<h4>{temp}&deg;C</h4>
		<h5>{"min- " + mtemp}&deg;C</h5>
		<h5>{"max- " + mmtemp}&deg;C</h5> </div>
		:"City Not Found:{"
		}
		</>
	)
	}

 export default Joke