import React,{useState} from 'react'
import axios from 'axios'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import {NavLink} from 'react-router-dom'
import Joke from './Joke'
import Fact from './Fact'
import Temp from './Temp'
const Quote= ()=>{
	const [data,setdata]=useState()
	const [ans,setans] = useState()
	const getQuote =async ()=>{
			const res = await axios.get('https://type.fit/api/quotes')
			await setdata(res.data)
			// console.log(data)
			
	}
	// console.log(data)
	if(!data){
	getQuote()}
	if(!ans && data){
		setans(data[Math.floor(Math.random()*data.length)])
	}
	const replay=()=>{
		setans(data[Math.floor(Math.random()*data.length)])	
	}
	return (<>
		<h1 className='menu' style={{backgroundColor: 'skyblue', color:'red',marginTop:"-7px"}}>
		<NavLink exact activeClassName='active_class' to='/game/api' style={{marginLeft: '10px'}}>API</NavLink>
		<NavLink exact activeClassName='active_class' to='/game/XOX' style={{marginLeft: '10px'}}>XOX</NavLink>
		<NavLink exact activeClassName='active_class' to='/game/Car' style={{marginLeft: '10px'}}>Car</NavLink>
		</h1>
		<div className="main_div" style={{flexDirection:'row',color:'black'}}>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: '#cfe2f3',
		}}>
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<Temp/></div>
		</div>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: 'rgb(180,255,180)',

		}}>
		{
		(data && ans) ? 
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<span>{'"'+ans['text']+'"'}</span>
		<span>{'-'+ans['author']}</span>
		<button onClick={replay} className='button_three' style={{margin:"5px"}}> <NavigateNextIcon/></button></div>
		:"loading"
		}
		</div>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			height: "350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: '#ead1dc',
		}}>
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<Joke/></div>
		</div>
		<div className="center_div" style={{
			margin:  "20px",
			padding: '20px',
			// minHeight: "350px",
			// maxHeight: " 350px",
			height:"350px",
			width: "285px",
			display: "flex",
			flexDirection: 'column',
			justifyContent: "center",
			alignItems: 'left',
			textAlign: 'left',
			backgroundColor: '#ffe599',
		}}>
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<Fact/></div>
		</div>
		
		</div></>
	)
	}

 export default Quote