import React,{useState} from 'react'
import axios from 'axios'
import NavigateNextIcon from '@material-ui/icons/NavigateNext';

const Joke= ()=>{

	const [data,setdata]=useState()
	const [ans,setans] = useState()
	const getJoke =async ()=>{
			const res = await axios.get('https://raw.githubusercontent.com/15Dkatz/official_joke_api/master/jokes/index.json')
			await setdata(res.data)
			// console.log(data)
	}
	// console.log(data)
	if(!data){
	getJoke()}
	if(!ans && data){
		setans(data[Math.floor(Math.random()*data.length)])
	}
	const replay=()=>{
		setans(data[Math.floor(Math.random()*data.length)])	
	}
	return (<>
		{
		(data && ans) ? 
		<div style={{display:"flex", flexDirection:"column",alignItems:"center"}} >
		<span>{ans['setup']}</span>
		<span>{ans['punchline']}</span>
		<button onClick={replay} className='button_three' style={{margin:"5px"}}> <NavigateNextIcon/></button></div>
		:"loading"
		}
		</>
	)
	}

 export default Joke